<template>
  <van-row>
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">视频播放</p>
      </van-col>
    </van-row>
    <!-- <video id="video"
           style="width:100%"></video> -->
    <video id="video"
           style="width:100%"
           x5-playsinline=""
           playsinline=""
           webkit-playsinline=""
           x-webkit-airplay="allow"
           preload="true"></video>
  </van-row>
</template>

<script>
import Hls from "hls.js";
export default {
  name: "index",
  data () {
    return {
      hls: null,
      video: null,
    }
  },
  mounted () {
    this.init()
  },
  beforeDestroy () {
    this.hls = null
    this.video = null
  }, //生命周期 - 销毁之前
  methods: {
    init () {
      this.video = document.getElementById('video');
      if (Hls.isSupported()) {
        console.log("11111", this.$route.query.url);
        this.hls = new Hls();
        this.hls.loadSource(this.$route.query.url);
        this.hls.attachMedia(this.video);
        this.video.play();
      } else if (this.video.canPlayType('application/vnd.apple.mpegurl')) {
        let url = this.$route.query.url
        console.log("22222", url);
        this.video.src = url
        // this.video.play();
      } else {
        console.log("无法初始化播放视频");
      }
    },
    play () {
      this.video.play();
    },
    back () {
      history.go(-1)
    }
  }
}
</script>

<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
$noticeBarHeight: px2rem(50);
.dt-title {
  // position: fixed;
  // top: 0;
  // z-index: 3;
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}
.dt-div {
  width: 100px;
  height: 100px;
  background-color: red;
}
</style>
